import React from 'react';
import Tabletop from 'tabletop';

const publicSpreadsheetUrl = 'https://docs.google.com/spreadsheets/d/1cfTVusugnon9mJ8jCFUMqfHhCm-SkUfrw6I1yknsg_s/edit?usp=sharing';

class Home extends React.Component {

  constructor() {
    super()
    this.state = {
      postVisible: false,
      data: [[0],[1],[2],[3]]
    }
  }

  componentWillMount(){
    this.init()
  }

  init() {
  Tabletop.init( {
    key: publicSpreadsheetUrl,
    callback: googleData => {
      this.setState({
        data: googleData
        })
      },
    simpleSheet: true 
    })
  }

  showPost() {
    this.setState(prevState => ({
      postVisible: !prevState.postVisible
    }));
  }

  render() {
    const { data } = this.state
    const rangeArray = [data[0], data[1], data[2], data[3]]

    return(
    rangeArray.map(obj => {
      let imagem = obj.Imagem;
      let urlId = '#' + obj.id;
      
      const photoStyle = {
        backgroundImage: `url(${imagem})`
      }
      
      return (
        <div>
          <div className={(this.state.postVisible ? 'none' : 'blog-card show')}>
            <div className="meta">
              <div className="photo" style={photoStyle}></div>
              <ul className="details">
                <li className="author"><a href={urlId} onClick={(obj) => this.showPost(obj.id)}>{obj.Autor}</a></li>
                <li className="date">{obj.Data}</li>
              </ul>
            </div>
            <div className="description">
              <h1><a key={obj.title} href={urlId} onClick={(obj) => this.showPost(obj.id)}>{obj.title}</a></h1>
              <h2>{obj.Categoria}</h2>
              <p> {obj.Descricao}</p>
              <p className="read-more">
                <a href={urlId} onClick={(obj) => this.showPost(obj.id)}>Leia Mais</a>
              </p>
            </div>
          </div>

          <div id={obj.id} className={(this.state.postVisible ? 'container' : 'none')}>
            <a name={obj.id}>
            <div className="text-center jumbotron">
              <h1 className="title">- {obj.title} -</h1>
              <img className="post-img" alt={obj.title} src={imagem}></img>
              <p className="lead">{obj.Artigo}</p>
              <a className="btn btn-lg btn-primary" href="/" role="button">Voltar &raquo;</a>
            </div>
            </a>
          </div>

        </div>
      );
    }))
  }
}

export default Home;