import React from 'react';

class About extends React.Component {
  render() {
      return(
        <div>
            <div className='container'>
              <div className="text-center jumbotron">
                <h1 className="title">- Quem sou Eu ? -</h1>
                <img className="img-about" alt="" src="http://c1.staticflickr.com/6/5564/15280164605_e2e7688f5a.jpg"></img>
                <p className="lead">Bem... Me chamo Vinicius, tenho 20 anos, passo o tempo estudando programação (o que, pensando bem, é um pouco estranho) e esse é apenas um projeto usando ReactJs + Google spreadsheet, através do Tabletop.js. Com esse projeto acredito que poderei aprender algumas coisas que usarei em trabalhos futuros para fazer templates, projetos onlines, guardar coordenadas para aplicar em um mapa de localização e coisas do gênero.</p>
                <p className="lead">Um dos meus objetivos nesse pequeno projeto foi aprender e simpatizar mais com o uso de javascript, levando em consideração que é a linguagem que mais cresce no mercado, além de entender o funcionamento da library mais usada no mundo JS, o React.</p>
                <a className="btn btn-lg btn-primary" href="/" role="button">Voltar aos Posts &raquo;</a>
              </div>
            </div>
        </div>
      )
  }    
}

export default About;